* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul{
  list-style: none;
}

.country__descrition{
  margin-bottom: 20px;
  list-style: none;
  display: flex;
  align-items: center;

  font-size: 20px;
  font-weight: 500;
}

span {
  font-size: 18px;
  font-weight: 500;
}